import React from 'react'
import './config'
import $ from 'jquery'
import 'antd-mobile/dist/antd-mobile.css'
import {ActivityIndicator,Card,List,Button,SearchBar} from 'antd-mobile';

const Item = List.Item;

class SearchPage extends React.Component{
    constructor(props){
        super(props);
        this.state={
            searchName:'',
            value:'',
        };
    }
    componentDidMount() {
        this.autoFocusInst.focus();
    }
    onSubmit(value){
        this.setState({searchName:value})
    }

    render(){
        return(
            <div>
                <SearchBar  onSubmit={value=>this.onSubmit(value)} placeholder="搜索" ref={ref => this.autoFocusInst = ref} />
                        <div>
                        <Card>
                            <Card.Header title="第一集群"/>
                            <Card.Body><SM name={this.state.searchName} searchen='kuyuncc'/></Card.Body>
                        </Card>
                        <Card>
                            <Card.Header title="第二集群"/>
                            <Card.Body><SM name={this.state.searchName} searchen='33uudy'/></Card.Body>
                        </Card>
                        <Card>
                            <Card.Header title="第三集群"/>
                            <Card.Body><SM name={this.state.searchName} searchen='yongjiuzy'/></Card.Body>
                        </Card>
                        <Card>
                            <Card.Header title="第四集群"/>
                            <Card.Body><SM name={this.state.searchName} searchen='zuidazy'/></Card.Body>
                        </Card>
                        </div>
                    )
                           
            </div>
        )
    }
}

class SM extends React.Component{
    constructor(props){
        super(props);
        this.state={
            selectedName:'',
            open:false,
            searchName:'',
            loading1:true,
            srcList:{},
            searchen:props.searchen
        };
    }
    
  componentWillReceiveProps(props){
    this.state={
        selectedName:'',
        open:false,
        searchName:props.name,
        loading1:true,
        srcList:{},
        searchen:props.searchen
    };
    if(this.state.searchName!=''){
        $.ajax({
            type:'get',
            async:true,
            url:global.constants.website+'admin/getmessage?name='+this.state.searchName+'&search='+this.state.searchen,
            dataType:'json',
            crossDomain: true,
            xhrFields: {
                withCredentials: true
            },
            success:(result)=> {
                this.setState({
                    srcList:result,
                    loading1:false,
                })
            },
            error : function(errorMsg) {
                alert("加载数据失败！");
                console.log(errorMsg);
            }
        })
    }

}
    addlove(){
        $.ajax({
            type:'get',
            async:true,
            url:global.constants.website+'admin/addlove?name='+this.state.selectedName,
            dataType:'json',
            crossDomain: true,
            xhrFields: {
                withCredentials: true
            },
            success:()=> {
                alert("添加喜欢(❤ ω ❤)成功");
            },
            error : function(errorMsg) {
                alert("添加喜欢(❤ ω ❤)失败");
                console.log(errorMsg);
            }
        })
    }
    showModal(srcname){
        this.setState({
            selectedName:srcname,
        })
    }
    render(){
        if (this.state.loading1==true){
            return (
                <div>
                <ActivityIndicator text="加载资源库中..."/>
                </div>
            );
        }
        if(this.state.loading1==false && this.state.selectedName==''){
            return (
                <div>
                    {this.state.searchName}
                    <List renderHeader={() => '影视资源库'} className="my-list">
                    {Object.keys(this.state.srcList).map((onename,index)=>(
                        <Item onClick={()=>this.showModal(onename)}>{onename}</Item>
                    ))}
                    </List>

                </div>

            )
        }
        if(this.state.selectedName!=''){
            return(
                
                <div>
                    <Button type="warning" onClick={()=>this.setState({selectedName:''})}>返回</Button>
                    <Button type="primary" onClick={()=>this.addlove()}>喜欢(❤ ω ❤)</Button>
                    {Object.keys(this.state.srcList[this.state.selectedName]).map((key,index)=>{
                        var b=this.state.srcList[this.state.selectedName]
                        return (
                            <List renderHeader={key} className="my-list">
                            {Object.keys(b[key]).map((ep,epindex)=>{
                                var a=b[key]
                                return(
                                    <a href={a[ep]}><Item>{ep}</Item></a>
                                )
                            })}
                            </List>
                        )
                    })}
                </div>
            )
        }
    }
}
export default SearchPage